<script>
import axios from 'axios';
export default {
    name: 'GamerDetail',
    data() {
        return {
            gamerData: {}
        }
    },
    created() {
        console.log( this.$route.params.pid );
        this.getGamerData(this.$route.params.pid  );
    },
    methods: {
        getGamerData( pid ){
            axios
                .get( 'https://thedivisiontab.com/api/player.php?pid=' + pid )
                .then( response => this.gamerData = response.data );
        }
    }
}
